.projects {
    color: $white;
    background: url("https://images.unsplash.com/photo-1571171637578-41bc2dd41cd2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80")
        center center / cover;
    background-attachment: fixed;
    font-size: 1.5rem;
    padding: 2rem 2rem;
    position: relative;
    backface-visibility: hidden;
    transform: translatez(0);
    -webkit-transform: translatez(0);

    @include respond(tablet) {
        padding: 4rem 2rem;
    }

    @include respond(laptop) {
        padding: 4rem 8rem;
    }

    &:before {
        content: "";
        background: $black;
        opacity: 0.8;
        position: absolute;
        z-index: -1;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
    }

    h2 {
        margin-bottom: 2rem;
        text-align: center;
    }

    .content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        height: 100%;
        width: 100%;
        z-index: 1;

        &:after {
            content: "";
        }

        &:after,
        .project {
            width: 100%;

            @include respond(tablet) {
                width: calc(50% - 10px);
            }

            @include respond(laptop) {
                width: calc(33% - 10px);
            }
        }

        .project {
            background-color: $black;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            margin-bottom: 20px;

            a {
                text-decoration: none;
            }

            &:hover,
            &:focus {
                opacity: 0.9;
            }

            .project-image {
                width: 100%;
            }

            .project-inner {
                color: $white;
                padding: 10px 30px 30px;
            }

            .project-category {
                font-size: 0.8rem;
            }
        }
    }
}
