.header {
    color: $white;
    background: url("https://images.unsplash.com/photo-1544847558-3ccacb31ee7f?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80")
        center center / cover;
    background-attachment: fixed;
    font-size: 1.5rem;
    padding: 2rem;
    position: relative;
    backface-visibility: hidden;
    transform: translatez(0);
    -webkit-transform: translatez(0);

    @include flex(50vh);

    @include respond(tablet) {
        @include flex(100vh);
    }

    &:before {
        content: "";
        background: $black;
        opacity: 0.7;
        position: absolute;
        z-index: -1;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
    }

    .heading,
    .subheading {
        text-align: center;
        text-transform: uppercase;
        z-index: 1;
    }

    .heading {
        font-size: 2rem;
        line-height: 1.1;

        @include respond(tablet) {
            font-size: 3rem;
        }

        @include respond(laptop) {
            font-size: 4rem;
        }
    }

    .subheading {
        font-size: 0.8rem;
        line-height: 1.2;

        @include respond(tablet) {
            font-size: 0.9rem;
        }

        @include respond(laptop) {
            font-size: 1rem;
        }

        span {
            color: $orange;
        }
    }
}
